import { Box, Button as _Button, styled } from '@mui/material'
import { HEADER_HEIGHT, NAV_WIDTH, ROUTES } from '../../constants'
import React from 'react'
import { ReactComponent as IconHistory } from '../../assets/icon/IconHistory.svg'
import { ReactComponent as IconWhitelist } from '../../assets/icon/IconWhitelist.svg'
import { ReactComponent as IconUser } from '../../assets/icon/IconUser.svg'
import { useNavigate } from 'react-router-dom'

const Button = styled(_Button)({
  display: 'flex',
  flexDirection: 'column',
  justifyContent: 'center',
  color: '#FFFFFF',
  fontSize: '15px',
  height: '80px',
  width: '100%',
  ':disabled': {
    color: '#96C4FF',
    '& path': {
      fill: '#96C4FF',
    },
  },
  '& path': {
    fill: '#FFFFFF',
  },
})

const Navigator: React.FC<{ page: string }> = ({ page }) => {
  const navigate = useNavigate()

  return (
    <Box
      sx={{
        position: 'absolute',
        top: HEADER_HEIGHT,
        left: '0px',
        width: NAV_WIDTH,
        height: `calc(100% - ${HEADER_HEIGHT})`,
        background: '#0B4780',
      }}
    >
      <Button disabled={page === 'history'} onClick={() => navigate(ROUTES.history)}>
        <IconHistory />
        History
      </Button>
      <Button disabled={page === 'whitelist'} onClick={() => navigate(ROUTES.whitelist)}>
        <IconWhitelist />
        Whitelist
      </Button>
      <Button disabled={page === 'user'} onClick={() => navigate(ROUTES.user)}>
        <IconUser />
        User
      </Button>
    </Box>
  )
}

export default Navigator
